/* Blog page */
.templateBlog {
  article {
      margin: 0 0 30px;
  }
  .page-heading {
      text-align: center;
      line-height: 45px;
      font-weight: 700;
      margin-bottom: 45px;
  }
  .article-item {
      display: flex;
      flex-direction: column;
      height: 100%;
  }
  .article-body {
      height: 100%;
      display: flex;
      flex-direction: column;
  }
  .article-header {
      margin-bottom: 17px;
  }
  .release-info {
      font-size: var(--body-font-size);
      color: #a4a4a4;
      line-height: 19.1px;
      font-weight: 400;
      margin-bottom: 5px;
  }
  .article-title {
      font-weight: 600;
      line-height: 24.55px;
      margin: 0 0 14px;
      @include font-body;
  }
  .article-excerpt {
      line-height: 20px;
      font-weight: 400;
      margin-bottom: 20px;
  }
  .article-button {
      margin-top: auto;
      max-width: 200px;
      width: 100%;
      a {
          padding: 7px 5px;
          width: 100%;
      }
  }
  .second-row {
      margin-top: 10px;
      padding-top: 40px;
      border-top: 1px solid var(--body-border-color);
  }
}

.templateArticle {
  .boxed-wrapper #main-content {
      margin-top: 0;
  }
  .article-main-page {
      margin-top: 40px;
  }
  .article-sidebar {
      margin-top: 30px;
      @media (min-width: 768px) {
          margin-top: 0;
      }
      >div:not(:last-child) {
          margin:  0 0 30px;
      }
      .form-search-container {
          position: relative;
      }
      .form-search {
          position: relative;
          input {
              width: 100%;
          }
          button {
              @include ab(0, n, 0, 15px);
              display: flex;
              align-items: center;
              justify-content: center;
              padding: 0;
              svg {
                  stroke: var(--body-link);
              }
          }
      }
      .sb-recent-post {
          .sb-recent-post-title {
              margin-bottom: 15px;
              h5 {
                  line-height: 21.82px;
                  font-weight: 700;
                  @include font-body;
              }
          }
          .article-item {
              display: flex;
              &:not(:last-child) {
                  margin-bottom: 15px;
                  padding-bottom: 15px;
                  border-bottom: 1px solid var(--body-border-color);
              }
          }
          .article-header {
              flex: 0 0 104px;
          }
          .article-body {
              @include margin-start(12px);
              .article-title {
                  font-size: var(--body-font-size);
                  line-height: 19.1px;
                  margin: 0 0 4px;
                  font-weight: 600;
                  @include font-body;
              }
              .release-info {
                  font-size: calc(var(--body-font-size) - 2px);
                  line-height: 16.37px;
                  color: #a4a4a4;
              }
          }
      }
      .sb-tag {
          .sb-title {
              margin: 0 0 15px;
              font-size: calc(var(--body-font-size) + 2px);
              font-weight: 700;
              line-height: 21.82px;
              text-transform: uppercase;
              color: var(--body-link);
              @include font-body;
          }
          ul {
              margin: 0 -4px;
              display: flex;
              li {
                  @include list-style-reset;
                  padding: 0 4px;
                  a {
                      border: 1px solid var(--body-border-color);
                      padding: 5px 10px;
                      display: block;
                  }
              }
          }
      }
  }
  .article-heading {
      margin: 0 0 20px;
  }
  .page-title {
      line-height: 36px;
      font-weight: 600;
  }
  .release-info {
      line-height: 19.1px;
      color: #A4A4A4;
  }
  .article-body {
      strong {
          color: var(--body-link);
      }
      p {
          margin: 0 0 20px;
      }
  }
  .relate-article {
      border-top: 1px solid var(--body-border-color);
      padding-top: 20px;
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      >div {
          padding: 0;
      }
      .prev-article {
          @include padding-end(15px);
      }
      .next-article {
          @include padding-start(15px);
      }
      .article-item {
          @media (min-width: 768px) {
              display: flex;
              align-items: center;
          }
      }
      .article-header {
          @media (min-width: 768px) {
              flex: 0 0 104px;
          }
      }
      .article-relate-title {
          text-transform: uppercase;
          font-weight: 700;
          line-height: 19.1px;
          margin-bottom: 15px;
      }
      .article-body {
          padding-top: 12px;
          @include padding-start(0);
          @media (min-width: 768px) {
              padding-top: 0;
              @include padding-start(12px);
          }
          .article-title {
              line-height: 19.1px;
              font-weight: 600;
              font-size: var(--body-font-size);
              margin: 0 0 4px;
              @include font-body;
          }
          .release-info {
              font-size: calc(var(--body-font-size) - 2px);
              line-height: 16.37px;
              color: #a4a4a4;
          }
      }
      .next-article {
          text-align: end;
          .article-item {
              justify-content: flex-end;
          }
          .article-header {
              @media (min-width: 768px) {
                  order: 2;
              }
          }
          .article-body {
              @media (min-width: 768px) {
                  order: 1;
                  @include padding-start(0);
                  @include padding-end(12px);
              }
          }
      }
  }
  .article-bottom {
      border-top: 1px solid var(--body-border-color);
      padding-top: 20px;
      margin-top: 30px;
      h4 {
          font-weight: 700;
          line-height: 24.55px;
          @include font-body;
      }
      ul {
          margin: 20px 0;
          li {
              @include list-style-reset;
              .comment-author {
                  margin: 0 0 2px;
                  color: var(--body-link);
                  line-height: 19.1px;
                  font-weight: 600;
              }
              .comment-date {
                  font-size: calc(var(--body-font-size) - 2px);
                  line-height: 16.37px;
                  color: #a4a4a4;
              }
              .comment {
                  line-height: 21px;
                  p {
                      margin: 0 0 15px;
                      &:last-child {
                          margin: 0;
                      }
                  }
              }
          }
      }
  }
  #comment-form {
      h5 {
          color: var(--main-color);
          line-height: 21.82px;
          font-weight: 600;
          margin: 0 0 8px;
          @include font-body;
      }
      p {
          margin: 0 0 18px;
          font-size: calc(var(--body-font-size) - 2px);
          line-height: 16.37px;
          color: #a4a4a4;
      }
      .form-row {
          margin: 0 0 25px;
          label {
              display: block;
              color: var(--body-link);
              line-height: 19.1px;
              margin: 0 0 8px;
          }
          input {
              width: 100%;
          }
      }
      .form-button {
          display: block;
          text-align: end;
          button {
              max-width: 224px;
              width: 100%;
          }
          p {
              margin: 15px 0 0;
          }
      }
  }
}
/* End blog post page */